import React from 'react'
export default function Navbar() {
  return (
    <nav className="navbar navbar-expand-lg sticky-top" style={{"background":"rgb(42 42 50)"}}>
    <div className="container-fluid">
    <a className="navbar-brand" href="#vinay" style={{"color":"white"}}>Vinay's Portfolio</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className=" navb nav-link active" style={{"color":"white"}} aria-current="page" href="#vinay" onClick={()=>{
            window.scroll({
            top:0,
            behavior:"smooth"});}}>HOME</a>
        </li>
        <li className="nav-item" >
          <a className="navb nav-link" style={{"color":"white"}} href='#About'>ABOUT</a>
        </li>
        <li className="nav-item" >
          <a className="navb nav-link" style={{"color":"white"}} href='#Skills'>SKILLS</a>
        </li>
       
        <li className="nav-item" >
          <a className="navb nav-link" style={{"color":"white"}} href='#project'>PROJECT</a>
        </li>
        <li className="nav-item" >
          <a className="navb nav-link" style={{"color":"white"}} href='#Contact'>CONTACT</a>
        </li>
      </ul>
    </div>
  </div>
</nav>
  )
}
