import React,{useState,useRef} from 'react'
import swal from 'sweetalert';
import emailjs from '@emailjs/browser';

export default function Contact() {
  const [Name, setName] = useState("")
    const [Email, setEmail] = useState("")
    const [message, setmessage] = useState("hi")
    const form = useRef();
    const nameInput=document.querySelector("#user_name");
    const emailInput=document.querySelector("#user_email")
    const textWrite =document.querySelector("textarea")
    const sendEmail = (e) => {
        e.preventDefault();
        if (Name.length === 0) {
            console.log("enter the text")
            swal({
                title: "Please Enter the Information",
                icon: "sucess",
                button: "Done"
            })

        }
        else {
            emailjs.sendForm('service_q8qokoe', 'template_vnl5iid', form.current, 'b8ooaiL1mlIdQHc3K')
                .then((result) => {
                    console.log(result.text);
                    console.log(Name.length)
                    console.log(Email.length)
                    console.log(message.length)
                    nameInput.value = "";
                    emailInput.value="";
                    textWrite.value="";

                    swal({
                        title: "Thank You",
                        text: "We will contact you soon",
                        icon: "success",
                        button: "Done"
                    });

                }, (error) => {
                    console.log(error.text);
                });

        }
    };
  return (
    <>
      
        <div className='container container4' id='Contact'>
        <br />
            <h1 className='mt-4'>Contact Me</h1>
            <h5>Get in touch</h5>
        
        <div className="container5" data-aos="zoom-in" data-aos-delay="40" data-aos-offset="150">
          <form className="form mb-4" ref={form} onSubmit={sendEmail}>
            <input className="input" onChange={e => setName(e.target.value)} type="text" id="user_name" name="user_name" placeholder="Name" />
            <input className="input" type="text" onChange={e => setEmail(e.target.value)} id="user_email" name="user_email" placeholder="E-Mail I.D." />
            <textarea name="message" class="textarea" onChange={e => setmessage(e.target.value)} id='textarea' placeholder="Enter message"></textarea>
            <center><button type='submit' class="button" value="Send">Submit</button></center>
          </form>

            {/* <p className='mail'> <img src="./images/Gmail-logo.png" style={{ 'width': '4rem' }} alt="" />Vinaygupta4281@gmail.com</p>
            <p> <img src="./images/phone.png" style={{ "width": "2.5rem" }} alt="" />- 8218325639</p> */}

            <div class="card2 mb-3 mt-3">
              <a href="https://www.linkedin.com/in/vinay-gupta-186699249" class="socialContainer containerThree" target="_blank" rel="noreferrer"><img class="socialSvg linkdinSvg" src="https://cdn.pixabay.com/photo/2015/07/13/07/33/icons-842861_640.png" alt="" style={{ "width": "2rem" }} /></a>
              <a href="https://instagram.com/vinaygupta8586?igshid=ZDdkNTZiNTM=" className="socialContainer containerOne" target="_blank" rel="noreferrer"><img class="socialSvg instagramSvg" src="https://cdn.pixabay.com/photo/2016/08/01/21/00/icon-1562136_640.png" style={{ "width": "2rem", "background": "transparent" }} alt="" /></a>
              <a href="https://www.facebook.com/profile.php?id=100011981955983" class="socialContainer containerThree" target="_blank" rel="noreferrer"><img class="socialSvg linkdinSvg" src="https://cdn.pixabay.com/photo/2015/07/13/07/41/icons-842893_640.png" style={{ "width": "2rem" }} alt="" /></a>

            </div>
          </div>
        </div>




    </>
  )
}
