import React from 'react';
import { Typewriter } from 'react-simple-typewriter';
export default function Home() {

    return (
        < >
            <div id='vinay' className='me1' data-aos="fade-down-right" data-aos-delay="160" data-aos-offset="500">

                <div className='me'>
                    <h1> Hi There, I'm <br /><span>Vinay Gupta</span></h1>
                    <h4 style={{fontWeight: 'bold', "color":"red"}}>
                    
                    {/* Style will be inherited from the parent element */}
                    <Typewriter
                        words={['FRONT-END DEVELOPER', 'PYTHON PROGRAMMER']}
                        autostart='true'
                        loop='true'
                        cursorStyle='_'
                        typeSpeed={100}
                        deleteSpeed={60}
                        delaySpeed={1000}
                        
                    />
                    .
                </h4>
                <br />
                <br />
                </div>
                <div className='images'>
                    <img style={{"opacity":"1"}} src="./images/me.jpg" alt="" />
                </div>
            </div>
        </>
    )
}
